import react, {Component} from 'react';
import {Navbar, NavbarBrand, NavbarItem, NavbarMenu, NavbarStart} from 'bloomer';



const myNav={
    border: '',
    backgroundColor: '#303F9F',
    color: 'white'
}
const mytext={
    color: 'white'
}


export default class navbar extends Component{
  
    render(){
        return(<Navbar style={mytext}>
            <NavbarBrand>
                <NavbarItem style={{color: 'white'}}>
                    Football coin
                </NavbarItem>
            </NavbarBrand>
            <NavbarMenu>
                <NavbarStart>
                    <NavbarItem  style={mytext}>
                        Home
                    </NavbarItem>
                </NavbarStart>
            </NavbarMenu>
        </Navbar>)
    }
}


